class CourseTime {
  constructor(startAt) {
    this.startAt = new Date(startAt)
  }

  currentTime() {
    return moment()
  }

  startTime() {
    return moment(this.startAt)
  }

  formatedStartTime() {
    return this.startTime().format("YYYY-MM-DD HH:mm:ss")
  }

  diff() {
    let current = this.currentTime().toDate()
    let start = this.startAt
    return current.getTime() - start.getTime()
  }

  getHour() {
    return parseInt(this.diff() / (60 * 60 * 1000))
  }

  diffTime() {
    return moment(this.diff())
  }
}

let course = new CourseTime("2015-12-07T00:50:00")

class Separator extends React.Component {
  render() {
    return (
      React.createElement("span", null, ":")
    )
  }
}

class Second extends React.Component {
  render() {
    return (
      React.createElement("span", null, this.props.second)
    )
  }
}

class Minute extends React.Component {
  render() {
    return (
      React.createElement("span", null, this.props.minute)
    )
  }
}

class Hour extends React.Component {
  render() {
    return (
      React.createElement("span", null, this.props.hour)
    )
  }
}

class Timer extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hour: course.getHour(),
      minute: course.diffTime().minute(),
      second: course.diffTime().second()
    }
  }

  componentDidMount() {
    setInterval(function(){
      this.setState({
        hour: course.getHour(),
        minute: course.diffTime().minute(),
        second: course.diffTime().second()
      })
    }.bind(this),1000)
  }

  render() {
    return (
    React.createElement("div", {className: "timer"}, 
      React.createElement(Hour, {hour: this.state.hour}), 
      React.createElement(Separator, null), 
      React.createElement(Minute, {minute: this.state.minute}), 
      React.createElement(Separator, null), 
      React.createElement(Second, {second: this.state.second})
    )
    )
  }
}

class StartTime extends React.component {
  render() {
    return (
      React.createElement("div", {className: "start-time"}, 
         course.formatedStartTime() 
      )
    )
  }
}

let Container = React.createClass({displayName: "Container",
  render: function() {
    return (
    React.createElement("div", null, 
      React.createElement(StartTime, null), 
      React.createElement(Timer, null)
    )
    )
  }
})

React.render(
  React.createElement(Container, null),
  document.getElementById('container')
)
